import React, { useCallback, useState } from "react";
import styled from "styled-components";
import { isCookieConsentSet, setCookieConsent } from "../helpers/cookies";

export const CookieConsent = () => {
  const [isCookieSet, setIsCookieSet] = useState(isCookieConsentSet());

  const handleAcceptClick = useCallback(() => {
    setCookieConsent();
    setIsCookieSet(true);
  }, []);

  if (isCookieSet) {
    return null;
  }

  return (
    <CookieWrapper data-test="cookie-consent">
      <div>
        We value your privacy, therefore we only use cookies to provide you with
        the best experience. By continuing to our site we will assume that
        you're happy with that.
      </div>
      <div>
        <StyledButton type="button" onClick={handleAcceptClick}>
          Ok
        </StyledButton>
      </div>
      <div>
        <a
          href="https://vosbor.com/privacy-policy/"
          target="_blank"
          rel="noreferrer"
        >
          Privacy policy
        </a>
      </div>
    </CookieWrapper>
  );
};

const CookieWrapper = styled.div`
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  overflow: hidden;
  background: #fff;
  color: #08134b;
  z-index: 10;
  padding: 16px;
  box-sizing: border-box;
  letter-spacing: 0.2px;
  -moz-osx-font-smoothing: grayscale;
  font-size: 14px;
  line-height: 1.17em;

  & > div + div {
    margin-left: 32px;
  }

  a {
    color: #4200ff;
    text-decoration: none;
    cursor: pointer;
  }
`;

const StyledButton = styled.button`
  color: #4200ff;
  border: 0;
  background: 0;
  cursor: pointer;
`;
